<template>
  <div class="userList">
    <el-row>
         <el-col :span="3">
        <el-input v-model.number="addRouter.username" placeholder="‰∫∫ÂëòÂêçÂ≠ó"></el-input>
      </el-col>
      <el-col :span="3">
        <el-input v-model.number="addRouter.childs" placeholder="Ë∑ØÁî±ÂêçÂ≠ó"></el-input>
      </el-col>
      <el-col :span="3">
        <el-input v-model.number="addRouter.router" placeholder="Ë∑ØÁî±Âú∞ÂùÄ"></el-input>
      </el-col>
      <el-col :span="3">
          <el-button @click="addRouters">Ê∑ªÂä†</el-button>
      </el-col>
    </el-row>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column type="index" width="180"></el-table-column>
      <el-table-column prop="name" label="Ê®°ÂùóË¥üË¥£‰∫∫" width="180"></el-table-column>
      <el-table-column label="‰∏öÂä°Ê®°Âùó">
        <template scope="solt">
          <el-tag
            :key="i"
            v-for="(v,i) in solt.row.childs"
            closable
            :disable-transitions="false"
            @close="delTag(v.id)"
          >
          <el-tag size="mini" type="success">Ê®°ÂùóÂêçÁß∞Ôºö{{v.names}}</el-tag> <br>
          <el-tag size="mini"  type="success">Ê®°ÂùóË∑ØÁî±Ôºö{{v.router}}</el-tag>
          </el-tag>
        </template>
      </el-table-column>
