<script>
export default {
  data() {
    return {
      tableData: [],
      inputVisible: false,
      inputValue: "",
      addRouter: {
          username:"",
          childs:"",
          router:""
      }
    };
  },
  methods: {
    getList() {
      this.$axios.get("/getUserList").then(res => {
        this.tableData = res.data;
      });
    },
    delTag(id){
         this.$confirm('Ê≠§Êìç‰ΩúÂ∞ÜÊ∞∏‰πÖÂà†Èô§Ê≠§Ë∑ØÁî±?', 'ÊèêÁ§∫', {
          confirmButtonText: 'Á°ÆÂÆö',
          cancelButtonText: 'ÂèñÊ∂à',
          type: 'warning'
        }).then(() => {
            this.$axios.delete('/getUserList/'+id).then(res=>{
                if(res.data.code == 200){
                this.getList()
                        this.$emit('getUserList')
                }
            })
          this.$message({
            type: 'success',
            message: 'Âà†Èô§ÊàêÂäü!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Â∑≤ÂèñÊ∂àÂà†Èô§'
          });          
        });
    },
    addRouters(){
        this.$axios.post("/getUserList",this.addRouter).then(res=>{
            if(res.data.code == 200){
                this.$message.success("Ê∑ªÂä†ÊàêÂäü")
                this.getList()
                // this.$parent.getUserList();
                 this.$emit('getUserList')
            }
        })
    }
  },
  created() {
    this.getList();
  }
};
</script>
